.welcome {
  width: 100%;
  height: 100vh;
  background: url("/images/mountain.jpg") no-repeat center center fixed;
  background-size: cover;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  position: relative;
}
.welcome .typed-text {
  color: #FFFFFF;
  text-align: center;
  font: 36px 'Raleway', sans-serif;
}
@media (max-width: 640px) {
  .welcome .typed-text {
    font-size: 24px;
  }
}
@media (max-width: 450px) {
  .welcome .typed-text {
    font-size: 18px;
  }
}


