.arrows {
  position: absolute;
  z-index: 3;
  left: 50%;
  top: 82%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
}
.arrows .arrow {
  width: 2vw;
  height: 2vw;
  border-left: 3px solid #c5b199;
  border-bottom: 3px solid #c5b199;
  position: absolute;
  top: 50px;
  left: 50%;
  -webkit-transform: rotate(-45deg);
      -ms-transform: rotate(-45deg);
          transform: rotate(-45deg);
}
@media (max-width: 680px) {
  .arrows .arrow {
    border-left: 1px solid #c5b199;
    border-bottom: 1px solid #c5b199;
  }
}
.arrows .arrow-1 {
  -webkit-animation: arrow-1-anim 1.5s infinite;
          animation: arrow-1-anim 1.5s infinite;
}
.arrows .arrow-2 {
  -webkit-animation: arrow-2-anim 1.5s infinite;
          animation: arrow-2-anim 1.5s infinite;
}
.arrows .arrow-3 {
  -webkit-animation: arrow-3-anim 1.5s infinite;
          animation: arrow-3-anim 1.5s infinite;
}
@-webkit-keyframes arrow-1-anim {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0.2;
  }
}
@keyframes arrow-1-anim {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0.2;
  }
}
@-webkit-keyframes arrow-2-anim {
  0% {
    -webkit-transform: translateY(0) rotate(-45deg);
            transform: translateY(0) rotate(-45deg);
  }
  99% {
    -webkit-transform: translateY(2vw) rotate(-45deg);
            transform: translateY(2vw) rotate(-45deg);
  }
  100% {
    -webkit-transform: translateY(0) rotate(-45deg);
            transform: translateY(0) rotate(-45deg);
  }
}
@keyframes arrow-2-anim {
  0% {
    -webkit-transform: translateY(0) rotate(-45deg);
            transform: translateY(0) rotate(-45deg);
  }
  99% {
    -webkit-transform: translateY(2vw) rotate(-45deg);
            transform: translateY(2vw) rotate(-45deg);
  }
  100% {
    -webkit-transform: translateY(0) rotate(-45deg);
            transform: translateY(0) rotate(-45deg);
  }
}
@-webkit-keyframes arrow-3-anim {
  0% {
    -webkit-transform: translateY(0) rotate(-45deg);
            transform: translateY(0) rotate(-45deg);
    opacity: 1;
  }
  99% {
    -webkit-transform: translateY(1vw) rotate(-45deg);
            transform: translateY(1vw) rotate(-45deg);
    opacity: 0.5;
  }
  100% {
    -webkit-transform: translateY(0) rotate(-45deg);
            transform: translateY(0) rotate(-45deg);
    opacity: 1;
  }
}
@keyframes arrow-3-anim {
  0% {
    -webkit-transform: translateY(0) rotate(-45deg);
            transform: translateY(0) rotate(-45deg);
    opacity: 1;
  }
  99% {
    -webkit-transform: translateY(1vw) rotate(-45deg);
            transform: translateY(1vw) rotate(-45deg);
    opacity: 0.5;
  }
  100% {
    -webkit-transform: translateY(0) rotate(-45deg);
            transform: translateY(0) rotate(-45deg);
    opacity: 1;
  }
}


